/* Default nav styles */
.nav {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}

.nav>ul {
  display: grid;
  grid-template-columns: 1fr 1fr 5fr 1fr 1fr;
  justify-content: center;
  align-items: center;
  list-style: none;
  text-transform: uppercase;
  margin: 2vh 4.3vw;
  font-weight: 500;
  position: relative;
}

.nav>ul>li {
  margin: 0 auto;
}

.nav>ul>li:nth-of-type(3) {
  grid-column: 4;
}

.nav>ul>li:nth-of-type(4) {
  grid-column: 3;
  grid-row: 1 / -1
}

.nav>ul>li:nth-of-type(5) {
  grid-column: 5;
}

.nav>ul>li>a>img {
  height: 13vh;
}

.hover-link {
  display: inline-block;
  text-decoration: none;
  font-family: 'Quicksand', sans-serif;
  font-size: 1.3rem;
  overflow: hidden;
  color: #0b1215;
  padding: 8px 12px;
}

.hover-link:hover,
.hover-link:active {
  color: #1f3337;
}

.hover-link span {
  display: inline-block;
  transition: transform 0.3s ease, color 0.3s ease;
  transform: translateY(0);
}

.hover-link:hover span {
  transform: translateY(-10px);
}

/* Delay letters */
.hover-link span:nth-child(1) {
  transition-delay: 0s;
}

.hover-link span:nth-child(2) {
  transition-delay: 0.05s;
}

.hover-link span:nth-child(3) {
  transition-delay: 0.1s;
}

.hover-link span:nth-child(4) {
  transition-delay: 0.15s;
}

.hover-link span:nth-child(5) {
  transition-delay: 0.2s;
}

.hover-link span:nth-child(6) {
  transition-delay: 0.25s;
}

.hover-link span:nth-child(7) {
  transition-delay: 0.3s;
}

.hover-link span:nth-child(8) {
  transition-delay: 0.35s;
}


.hamburger {
  background: none;
  border: none;
  cursor: pointer;
  z-index: 20;
}

.no-scroll {
  overflow: hidden;
}

.hidden-text {
  display: none;
}

.nav-header>.hover-img {
  display: none;
}

.mobile-logo>a {
  display: block;
  width: 100%;
  height: 100%;
}


.nav-header>.hover-img>img {
  height: 8vh;
}

.nav-links.open {
  display: flex;
  animation: slideDown 0.3s forwards;
}

.nav-links.closing {
  display: flex;
  animation: slideUp 0.3s forwards;
}

.nav>ul {
  display: grid;
  grid-template-columns: 1fr 1fr 5fr 1fr 1fr;
  justify-content: center;
  align-items: center;
  list-style: none;
  text-transform: uppercase;
  margin: 2vh 4.3vw;
  font-weight: 500;
  position: relative;
  z-index: 1;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    opacity: 1;
    transform: translateY(0);
  }

  to {
    opacity: 0;
    transform: translateY(-20px);
  }
}

@media only screen and (max-width: 800px) {
  .nav>ul {
    display: none;
    flex-direction: column;
    gap: 20px;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    padding: 20px 0;
    z-index: 9999;
  }

  .nav-header>.hover-img {
    display: block;
  }

  .nav>ul>li>a>span {
    color: #FFFAFA;
  }

  .nav-links.open {
    display: flex;
  }

  .hamburger {
    display: block;
  }

  .nav-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2vh 4.3vw;
  }

  .nav>ul>li {
    margin: 0;
  }

  .hover-link {
    font-size: 1.2rem;
    text-align: center;

    opacity: 1 !important;
    transform: none !important;
    transition: none !important;
  }

  .mobile-logo {
    display: none;
  }
}

@media only screen and (max-width: 1200px) {
  .hover-link {
    font-size: 1rem;
  }

  .nav>ul {
    grid-template-columns: 1fr 1fr 3fr 1fr 1fr;
  }
}